import { ChangeEvent, memo, useState, VFC } from "react";
import styles from "./Register.module.css";
import { Input, Button } from "semantic-ui-react";
import { Title } from "../Login/Title/Title";
import { useNavigate } from "react-router-dom";
import axios from "axios";
import { atom, useSetRecoilState } from "recoil";
// 
import { recoilPersist } from "recoil-persist";
// User
import { User } from "../../../types/RegisterType";
// 

// recoil-persist
const { persistAtom } = recoilPersist({
  key: "recoil-persist",
  storage: localStorage,
});

// atom
export const userLoginState = atom<User>({
  key: "userLoginState",
  default: {
    id: 0,
    user_id: 0,
    user_name: "",
    access_token: "",
  },
  effects_UNSTABLE: [persistAtom],
});

export const Register: VFC = memo(() => {
  const navigate = useNavigate();
  const [userName, setUserName] = useState("");
  const [password, setPassword] = useState("");
  const [confirmPassword, setConfirmPassword] = useState("");
  const [errorMessage, setErrorMessage] = useState("");

  // atom
  const setLogin = useSetRecoilState(userLoginState);

  //  
  const [isLoading, setIsLoading] = useState(false);

  // 
  const onChangeUserNameInput = (e: ChangeEvent<HTMLInputElement>) => {
    setUserName(e.target.value);
  };

  // 
  const onChangePasswordInput = (e: ChangeEvent<HTMLInputElement>) => {
    setPassword(e.target.value);
  };

  // 
  const onChangeConfirmPasswordInput = (e: ChangeEvent<HTMLInputElement>) => {
    setConfirmPassword(e.target.value);
  };

  const postRegisterUser = async () => {
    try {
      const result = await axios.post<User>(
        "http://localhost:5000/user/register",
        {
          user_name: userName,
          joined_date: new Date(),
          password: password,
        }
      );
      return result.data;
    } catch (err: any) {
      throw new Error(err);
    }
  };

  // URL/user/login /user/register
  const postLoginUser = async () => {
    try {
      const result = await axios.post<User>(
        "http://localhost:5000/user/login",
        {
          user_name: userName,
          password: password,
        }
      );
      return result.data;
    } catch (err: any) {
      throw new Error(err);
    }
  };

  const onClickSignUp = () => {
    if (password === confirmPassword) {
      if (isLoading) {
        return;
      }
      setIsLoading(true);
      postRegisterUser()
        .then((result) => {
          postLoginUser()
            .then((result) => {
              // atomRecoil
              setLogin(result);
              console.log(result);
              localStorage.setItem("token", result.access_token);
              navigate("/gamestart");
            })
            .catch((err) => console.log(err))
            .finally(() => setIsLoading(false));
        })
        .catch((err) => console.log(err))
        .finally(() => setIsLoading(false));
    } else {
      setErrorMessage("");
    }
  };

  return (
    <div className={styles.wrapper}>
      <div className={styles.container}>
        <Title text="SIGN UP" />
        {/* username */}
        <Input
          className={styles.form}
          type="text"
          focus
          fluid
          placeholder="UserName"
          onChange={onChangeUserNameInput}
        />
        {/* password */}
        <Input
          className={styles.form}
          type="password"
          focus
          fluid
          placeholder="Password"
          onChange={onChangePasswordInput}
          value={password}
        />
        {/* password */}
        <Input
          className={styles.form}
          type="password"
          focus
          fluid
          placeholder="Confirm Password"
          onChange={onChangeConfirmPasswordInput}
          value={confirmPassword}
        />
        <Button
          className={styles.signupbutton}
          size="small"
          onClick={onClickSignUp}
        >
          Sign up
        </Button>
      </div>
      <p className={styles.errorMessage}>{errorMessage}</p>
    </div>
  );
});
